
var tag = document.createElement('script');
tag.src = "https://www.youtube.com/iframe_api";
var firstScriptTag = document.getElementsByTagName('script')[0];
firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

let player;

const overlay = document.querySelector('.overlay');
const toPlay = document.getElementById('to-play');
const popupVideo = document.querySelector('.popup__video');
toPlay.addEventListener('click', () => {
  if (player) {
    overlay.classList.add('_active');
    popupVideo.classList.add('_active');
    player.playVideo();
  } else {
    player = new YT.Player('player', {
          playerVars: { 
              'autoplay': 0, 
              'controls': 1,
              'playsinline': 1
          },
  
          height: '100%',
          width: '100%',
          videoId: 'G8lVXBN8ZFk',
          events: {
          'onReady': onPlayerReady,
          // 'onStateChange': onPlayerStateChange
          }
      });
  }
});


const closeVideo = document.querySelector('.popup__close-video');

function onPlayerReady(e) {
  const video = e.target;
  e.target.playVideo();
  overlay.classList.add('_active');
  popupVideo.classList.add('_active');

  overlay.addEventListener('click', (e) => {
    if (e.target == overlay || e.target == closeVideo) {
      video.pauseVideo();
      overlay.classList.remove('_active');
      popupVideo.classList.remove('_active');
    }
  })
}
  
